.tileItem {
      width: 100px;
      height: 100px;
      text-align: center;
      line-height: 100px;
      border-radius: 5px;
      margin-right: 10px;
      float:left;
      font-size: 30px;
      font-family: "微软雅黑";
}
.rowWapper {
  margin-bottom: 10px;
  float: left;
}
.boxWrapper {
  width: 440px;
  overflow: hidden;
  margin: 100px auto;
  background-color: rgb(187, 173, 160);
  padding-left: 10px;
  padding-top: 10px;
  border-radius: 10px;
  /* box-sizing: border-box; */
}
.buttons {
  margin: 30px;
  width: 50px;
  float: left;
  cursor: pointer;
}